
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Previewer from 'vux/src/components/previewer/index.vue'

import { refreshMixin } from "../../common/js/mixin";
import { backMixin } from "../../common/js/picBack";

export default {
name: "OrderInfo",
mixins: [refreshMixin, backMixin],
components: {
XHeader,
Group,
Cell,
Grid,
GridItem,
Previewer
},
data() {
return {
options: {
saveImg(el) { }
},
ID: "",
info: {
DealDate: "",
Prods: []
},
imgFile: [],
showImgFile: [],
isShow: false,
payMethod: [
"未付",
"支票",
"网上银行",
"电付",
"邮局汇款",
"支付宝",
"其他"
],
url: "",
isMsg: false
};
},
filters: {

picUrl(path, w = 0, h = 0) {
return `${api50}/api/v1/File/DownLoadPic?filePath=${path}&w=${w}&h=${h}`;
}
},
mounted() {
this.ID = this.$route.query.id;
this.GetOrderInfoByID();

if (this.$route.query.url) {
this.url = this.$route.query.url;
}
this.$bus.on("refreshOrderList", () => {
this.GetOrderInfoByID();
});

},
beforeRouteEnter(to, from, next) {
let isMsg = false;
if (from.path == "/") {
isMsg = true;
if (window.plus) {
window.setTimeout(() => {
plus.key.removeEventListener("backbutton", () => { });
plus.key.addEventListener("backbutton", () => {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
});
}, 0);
} else {
document.addEventListener("plusready", () => {
plus.key.removeEventListener("backbutton", () => { });
plus.key.addEventListener("backbutton", () => {
var ws = plus.webview.currentWebview();
plus.webview.close(ws);
});
});
}
}
next(el => {
el.isMsg = isMsg;
});
},
methods: {
back() {
if (this.$route.query.backIndex && this.$route.query.backIndex == 1) {

var ws = plus.webview.currentWebview();
plus.webview.close(ws);
} else {
this.$router.back();
}
},
disposeTime(time) {
if (time != undefined || time != null) {
return time.replace(" 00:00:00", "");
}
},
GetOrderInfoByID() {
if (this.ID != undefined) {
axios
.get(`/Order/GetOrderInfoByID/${this.ID}`)
.then(res => {
if (res.data.State === 1) {
this.info = res.data.Data;
this.imgFile = []
res.data.Data.ImgAttach.forEach(item => {
this.imgFile.push(item);
this.showImgFile.push({
src: `${api50}/api/v1/File/DownLoadPic?filePath=${
item.FilePath
}`
});
});
} else {

}
})
.catch(e => {




});
}
},
imgShow(index) {
this.$refs.previewer.show(index);
},
goto() {
if (this.info.OrderStateName == "已确认") {
this.$vux.toast.show({
text: "已确认订单不允许修改",
type: "warn"
});
return;
} else {
this.$router.push(
`orderInfo/changeOrder?id=${this.info.ID}&url=${this.url}`
);
this.isShow = false;
}
},
delOrder() {







const _this = this; 
this.$vux.confirm.show({
title: "删除订单",
content: "确认删除订单?",

onCancel() { },
onConfirm() {
axios
.post(`/Order/DeleteOrderByID/${_this.ID}`)
.then(res => {
if (res.data.State === 1) {
_this.$bus.emit("refreshOrderList")
_this.$vux.toast.show({
text: "删除成功!",
type: "success"
});
setTimeout(() => {
_this.$router.back();
_this.$vux.toast.hide()
}, 1000);
} else {
_this.$vux.toast.show({
text: res.data.ErrorMessage,
type: "warn"
});
}
})
.catch(e => { });
}
});
},
isDel(val) {
return (this.$store.state.isAdmin || (val ? (new Date().getTime() - new Date(val.replace(/-/g, "/")).getTime() <= 86400000) : ''));
}
}
};



